html {
  box-sizing: border-box;
  background-color: grey;
  overflow-x: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  padding: 0;
  margin: 0 auto;
  font-family: "firasans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: black;
  background-color: white; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

@font-face {
  font-family: "firasans";
  src: local("firasans regular"), local("firasans-regular"), url("../fonts/firasans-regular.woff2") format("woff2"), url("../fonts/firasans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans bold"), local("firasans-bold"), url("../fonts/firasans-bold.woff2") format("woff2"), url("../fonts/firasans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans semibold"), local("firasans-semibold"), url("../fonts/firasans-semibold.woff2") format("woff2"), url("../fonts/firasans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "firasans";
  src: local("firasans medium"), local("firasans-medium"), url("../fonts/firasans-medium.woff2") format("woff2"), url("../fonts/firasans-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

.page-header {
  background-color: brown;
  background-image: url("../img/intro-bg-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 906px;
  padding-left: 110px;
  padding-right: 114px;
  padding-top: 32px; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .page-header {
      min-height: 600px;
      background-image: url("../img/intro-bg-desktop@1x.jpg");
      background-position: center center; } }
  @media (max-width: 767px) {
    .page-header {
      background-image: url("../img/intro-bg-mobile@1x.jpg");
      min-height: 636px;
      padding-left: 16px;
      padding-right: 7px;
      padding-top: 20px; } }
  .page-header p {
    text-align: center;
    color: white;
    font-size: 24px;
    font-weight: bold;
    width: 343px;
    margin: 0 auto;
    margin-top: 458px; }
    @media (min-width: 768px) and (max-width: 1023px) {
      .page-header p {
        margin-top: 300px; } }

.page-header__callback-link {
  color: black;
  border-bottom: 1px dashed black;
  display: inline; }
  @media (max-width: 767px) {
    .page-header__callback-link {
      width: auto; } }

.page-header__site-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap; }
  @media (max-width: 767px) {
    .page-header__site-nav {
      flex-wrap: wrap;
      align-items: end; } }

.page-header__logo {
  margin: 0 auto;
  height: 48px;
  margin-top: -40px; }
  @media (max-width: 767px) {
    .page-header__logo {
      order: -1;
      width: 50%;
      margin-top: 0; } }
  @media (max-width: 767px) {
    .page-header__logo img {
      width: 120px;
      height: 42px; } }

.page-header__phone-links {
  display: flex;
  justify-content: space-between;
  width: 100%; }
  .page-header__phone-links a {
    color: black; }
    @media (max-width: 767px) {
      .page-header__phone-links a {
        margin-left: auto; } }
  @media (max-width: 767px) {
    .page-header__phone-links {
      width: 50%;
      flex-wrap: wrap; } }
